.header h1 {
  color: #502050;
}
.header p {
  color: #937b92;
}

.stars {
  background-color: #f7f2f7;
}
.stars h5 {
  color: #502050;
}

.purple {
  background-color: #502050;
  color: white;
}

.text span {
  color: #ee68a4;
}

html {
  box-sizing: border-box;
  font-size: 100%;
}

*, *:before, *:after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

@media only screen and (min-width: 1400px) {
  body {
    position: relative;
    min-height: 100vh;
    padding: 0 0;
  }
}

@media only screen and (min-width: 1400px) {
  main {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: grid;
    grid-template: repeat(2, 1fr)/repeat(2, 600px);
  }
}
@media only screen and (min-width: 1440px) {
  main {
    grid-template: repeat(2, 320px)/repeat(2, 600px);
    grid-gap: 50px;
  }
}

@media only screen and (min-width: 600px) {
  .ratings {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
}
@media only screen and (min-width: 1440px) {
  .ratings {
    display: flex;
  }
}

@media only screen and (min-width: 600px) {
  .stars {
    width: 480px;
    display: flex;
    align-items: center;
  }
  .stars .star-img {
    margin-left: 40px;
    margin-right: 50px;
  }
  .stars img {
    height: 1em;
  }
}

@media only screen and (min-width: 1440px) {
  .box1 {
    align-self: flex-start;
  }
}

@media only screen and (min-width: 1440px) {
  .box2 {
    justify-self: center;
  }
}

@media only screen and (min-width: 1440px) {
  .box3 {
    align-self: flex-end;
  }
}

@media only screen and (min-width: 600px) {
  .testimonials {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
}
@media only screen and (min-width: 1400px) {
  .testimonials {
    grid-column: 1/-1;
    grid-row: 2;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
}

@media only screen and (min-width: 600px) {
  .purple {
    width: 370px;
    height: 260px;
    display: flex;
  }
}
@media only screen and (min-width: 1440px) {
  .purple {
    width: 395px;
    height: 270px;
    display: flex;
  }
}

@media only screen and (min-width: 1440px) {
  .colton {
    align-self: flex-start;
    justify-self: flex-start;
  }
}

@media only screen and (min-width: 1440px) {
  .irene {
    align-self: center;
    justify-self: center;
  }
}

@media only screen and (min-width: 1440px) {
  .anne {
    align-self: flex-end;
    justify-self: flex-end;
  }
}

.pfp img {
  width: 45px;
  height: 45px;
  border-radius: 50%;
  margin-right: 30px;
}

